// ** React Imports
import { Fragment, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import themeConfig from '@src/configs/themeConfig'
// ** Custom Hooks
import { useSkin } from '@hooks/useSkin'
import useJwt from '@src/auth/jwt/useJwt'

// ** Third Party Components
import toast from 'react-hot-toast'
import { useDispatch } from 'react-redux'
import { useForm, Controller } from 'react-hook-form'
import { Coffee, X } from 'react-feather'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Actions
import { handleLogin } from '@store/authentication'

// ** Custom Components
import Avatar from '@components/avatar'
import InputPasswordToggle from '@components/input-password-toggle'

// ** Utils
import { getHomeRouteForLoggedInUser } from '@utils'

// ** Reactstrap Imports
import { Row, Col, Form, Input, Label, Button, CardText, CardTitle, FormFeedback } from 'reactstrap'

// ** Styles
import '@styles/react/pages/page-authentication.scss'
import axios from '@src/configs/axios/axiosConfig'

const ToastContent = ({ t, name }) => {
  return (
    <div className='d-flex'>
      <div className='me-1'>
        <Avatar size='sm' color='success' icon={<Coffee size={12} />} />
      </div>
      <div className='d-flex flex-column'>
        <div className='d-flex justify-content-between'>
          <h6>{name}</h6>
          <X size={12} className='cursor-pointer' onClick={() => toast.dismiss(t.id)} />
        </div>
        <span>You have successfully logged in as an user to Turia. Now you can start to explore. Enjoy!</span>
      </div>
    </div>
  )
}

const defaultValues = {
  password: '',
  email: ''
}

const Login = () => {
  // ** Hooks
  const { skin } = useSkin()
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const {
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({ defaultValues })
  const illustration = skin === 'dark' ? 'login-v2-dark.svg' : 'login-v2.svg',
    source = require(`@src/assets/images/pages/${illustration}`).default

  const getOrganization = async (data) => {
    const user = { userId: data.users.id }
    const res = await axios.post('organizationusers/usersorganization', user)

    if (res.data.organizations.length === 0) {
      navigate('/organization/create')
    } else if (res.data.organizations.length > 0) {
      const activeOrg = res.data.organizations.find((obj) => obj.isdefault === true)
      localStorage.setItem('activeOrganization', JSON.stringify(activeOrg || res.data.organizations[0]))
      navigate(getHomeRouteForLoggedInUser('admin'))
    }
  }
  const onSubmit = data => {
    if (Object.values(data).every(field => field.length > 0)) {
      setBlock(true)
      useJwt
        .login({ email: data.email, password: data.password })
        .then(async (res) => {
          setBlock(false)
          const userData = res.data
          await dispatch(handleLogin(userData))
          await getOrganization(userData)
          toast(t => (
            <ToastContent t={t} name={userData.users.name} />
          ))
        })
        .catch(err => {
          setBlock(false)
          Object.keys(err.response.data.errors).forEach(key => {
            setError(key, { type: 'custom', message: err.response.data.errors[key][0] })
          })
        })
    } else {
      for (const key in data) {
        if (data[key].length === 0) {
          setError(key, {
            type: 'manual',
            message: `Please Enter a ${key}`
          })
        }
      }
    }
  }

  return (
    <div className='auth-wrapper auth-cover'>
      <UILoader blocking={block} loader={<Loader />}>
        <Row className='auth-inner m-0'>
          <Link className='brand-logo' to='/' onClick={e => e.preventDefault()}>
            <img src={themeConfig.app.appLogoImage} alt={'Turia Logo'} width={45} height={35} />
            <h1 className='brand-text text-primary'>Turia</h1>
          </Link>
          <Col className='d-none d-lg-flex align-items-center p-5' lg='8' sm='12'>
            <div className='w-100 d-lg-flex align-items-center justify-content-center px-5'>
              <img className='img-fluid' src={source} alt='Login Cover' />
            </div>
          </Col>
          <Col className='d-flex align-items-center auth-bg px-2 p-lg-5' lg='4' sm='12'>
            <Col className='px-xl-2 mx-auto' sm='8' md='6' lg='12'>
              <CardTitle tag='h2' className='fw-bold mb-1'>
                Welcome to Turia! 
              </CardTitle>
              <CardText className='mb-2'>Please sign-in to your account and start the adventure</CardText>
              <Form className='auth-login-form mt-2' onSubmit={handleSubmit(onSubmit)}>

                {errors['action'] && <p className='text-danger'>{errors['action']?.message}</p>}
                <div className='mb-1'>
                  <Label className='form-label' for='email'>
                    Email
                  </Label>
                  <Controller
                    id='email'
                    name='email'
                    control={control}
                    render={({ field }) => (
                      <Input
                        autoFocus
                        type='email'
                        placeholder='john@example.com'
                        invalid={errors.email && true}
                        {...field}
                      />
                    )}
                  />
                  {errors['email'] && <FormFeedback>{errors['email']?.message}</FormFeedback>}
                </div>
                <div className='mb-1'>
                  <div className='d-flex justify-content-between'>
                    <Label className='form-label' for='login-password'>
                      Password
                    </Label>
                    <Link to='/forgot-password'>
                      <small>Forgot Password?</small>
                    </Link>
                  </div>
                  <Controller
                    id='password'
                    name='password'
                    control={control}
                    render={({ field }) => (
                      <InputPasswordToggle className='input-group-merge' invalid={errors.password && true} {...field} />
                    )}
                  />
                  {errors['password'] && <FormFeedback>{errors['password']?.message}</FormFeedback>}
                </div>
                <div className='form-check mb-1'>
                  <Input type='checkbox' id='remember-me' />
                  <Label className='form-check-label' for='remember-me'>
                    Remember Me
                  </Label>
                </div>
                <Button type='submit' color='primary' block>
                  Sign in
                </Button>
              </Form>
              <p className='text-center mt-2'>
                <span className='me-25'>New on our platform?</span>
                <Link to='/register'>
                  <span>Create an account</span>
                </Link>
              </p>
            </Col>
          </Col>
        </Row>
      </UILoader>
    </div>
  )
}

export default Login
